import React from "react";
import styled from "styled-components";
import { Box } from "theme-ui";
import { colorMode, ThemeColor } from "../../theme/colors";

export interface CircleBarProps {
  showValue?: boolean;
  title: string;
  themeColor: ThemeColor;
  percent: number;
}

export const CircleProgressBar = ({ themeColor = "default", percent = 0 }) => {
  return (
    <CircleProgressBarBase
      //@ts-ignore
      $colorMode={colorMode[themeColor]}
      $percent={100 - percent}
    >
      <svg
        className="circleContainer"
        viewBox="2 -2 28 36"
        xmlns="http://www.w3.org/2000/svg"
      >
        <circle
          className="circleContainerBackground"
          r="16"
          cx="16"
          cy="16"
        ></circle>
        <circle className="circleOffset" r="16" cx="16" cy="16"></circle>
      </svg>
      <Percent>{percent}%</Percent>
    </CircleProgressBarBase>
  );
};

const swatches = {
  blue: "#0091FF",
  blueBright: "#3e8ff7",
  pink: "#D356B9",
  pinkGmi: "#EC5B94",
  red: "#FF562B",
  orange: "#D87C22",
  yellow: "#FFC503",
  green: "#1BB441",
  greenPrice: "#68CE81",
  bluePrice: "#50A7E9",
  redPrice: "#F55858",
  purple: "#7B61FF",
  black: "#000",
  white: "#FFF",
  "grey-200": "#F5F5F5",
  "grey-300": "#E4E4E4",
  "grey-400": "#C0C0C0",
  "grey-500": "#A7A7A7",
  "grey-600": "#7C7C7C",
  "grey-700": "#545454",
  "grey-800": "#231F20",
  "grey-900": "#151515",
  transparent: "transparent",
};

const colors = {
  ...swatches,
  /* Aliases */
  primary: swatches.blue,
  secondary: swatches.pink,
  background: swatches.black,
  error: swatches.red,
  text: swatches["grey-300"],
  disabled: swatches["grey-700"],
};

export default colors;

interface CircleBarBaseProps {
  showValue?: boolean;
  $percent: number;
  $colorMode: Record<string, string>;
}

const Percent = styled.div`
  position: fixed;
  top: 15%;
  left: 5%;
  color: white;
  font-size: 20px;
  z-index: 1;
`;

export const CircleProgressBarBase = styled.div<CircleBarBaseProps>`
  position: relative;
  width: inherit;
  height: inherit;

  .circleContainer {
    --dot-diameter: 100%;
    --circle-border-width: 4px;
    --default-color: #545454;
    --completion-color: ${({ $colorMode }) => $colorMode?.midtone};
    width: var(--dot-diameter);
    height: var(--dot-diameter);
    transform: rotate(-90deg);
  }
  .circleContainerBackground {
    fill: none;
    stroke: var(--default-color);
    stroke-width: var(--circle-border-width);
  }
  .circleOffset {
    fill: none;
    stroke-linecap: round;
    stroke: var(--completion-color);
    stroke-dasharray: 100 100;
    stroke-linecap: round;
    stroke-width: var(--circle-border-width);
    stroke-dashoffset: ${({ $percent }) => $percent};
  }
`;
